    <GoodsHeader name="top"/>
    <!---->
    <GoodSwiper/>
    <!---->
    <GoodsRecommend/>
    <!---->
    <GoodsRecom/>
    <!---->
    <GoodsCarve/>
    <!---->
    <GoodsChoose/>
