    components:{
      GoodsHeader,
      GoodSwiper,
      GoodsRecommend,
      GoodsRecom,
      GoodsCarve,
      GoodsChoose,
      GoodLook,
      GoodFindMore
    }
  }
</script>
<style lang="less">
  @import "../../common/less/mixins";
  .goodscon-wrap{
    background-color: #eeeeee;
    .goTop{
      background-image: url('./img/top.png');
      background-size: 100% 100%;
      width:82/@rem;
      height:82/@rem;
      position: fixed;
      bottom: 120/@rem;
      right:30/@rem;
      z-index: 20;
    }
  }
</style>



